.programmes__list {
  .list-reset();
  display: grid;
  row-gap: 20px;
  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
    column-gap: 80px;
    row-gap: 40px;
  }
}

.programmes__item {
  display: grid;
  grid-template-columns: 75px 1fr;
  padding: 24px 20px 11px;
  background-color: #f2f2f2;
  @media (min-width: @tablet-width) {
    grid-template-columns: 1fr 300px;
    padding: 77px 52px 52px;
    column-gap: 19px;
  }
  @media (min-width: @desktop-width) {
    grid-template-columns: 150px 1fr;
    column-gap: 47px;
  }
}

.programmes__item::before {
  content: "";
  display: block;
  height: 50px;
  width: 67px;
  transform: translate(-14%, -5%);
  background: ~'url("../img/programmes-cat-slim.svg")' no-repeat top / contain;
  grid-column: 1;
  grid-row: 1;
  z-index: 1;
  @media (min-width: @tablet-width) {
    width: 268px;
    height: 200px;
    grid-column: 2;
    grid-row: 1 / 3;
  }
  @media (min-width: @desktop-width) {
    width: 134px;
    height: 100px;
    grid-column: 1;
    grid-row: 1;
  }
}

.programmes__item--fat::before {
  background-image: ~'url("../img/programmes-cat-fat.svg")';
  transform: translate(-11%, 22%);
}

.programmes__item::after {
  content: "";
  display: block;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background-color: @background-color-secondary;
  grid-column: 1;
  grid-row: 1;
  @media (min-width: @tablet-width) {
    width: 200px;
    height: 200px;
    grid-column: 2;
    grid-row: 1 / 3;
  }
  @media (min-width: @desktop-width) {
    width: 100px;
    height: 100px;
    grid-column: 1;
    grid-row: 1;
  }
}

.programmes__item-title {
  .font(24px,37px);
  .flex-row-align();
  margin: 0 0 0 -4px;
  color: @text-color-thirdly;
  font-family: @font-family-secondary;
  text-transform: uppercase;
  grid-column: 2;
  @media (min-width: @tablet-width) {
    grid-column: 1;
  }
  @media (min-width: @desktop-width) {
    grid-column: 2;
  }
}

.programmes__item-desc {
  .flex-column();
  margin: 22px 0 0;
  grid-column: span 2;
  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background-color: #d9d9d9;
    margin: 23px 0 9px;
  }
  @media (min-width: @tablet-width) {
    grid-column: 1;
    &::after {
      display: none;
    }
  }
  @media (min-width: @desktop-width) {
    grid-column: 1 / -1;
  }
}

.programmes__item-button {
  .font(16px, 16px);
  .flex-row-align();
  justify-content: flex-start;
  text-align: left;
  max-width: fit-content;
  padding: 10px 22px 10px 0;
  grid-column: span 2;
  &:hover {
    opacity: 0.6;
    svg {
      transition: transform 0.1s;
      transform: translateX(3px);
    }
  }
  &:active {
    opacity: 0.3;
  }
  svg {
    margin-left: 16px;
  }
  @media (min-width: @tablet-width) {
    grid-column: 1;
  }
  @media (min-width: @desktop-width) {
    grid-column: 1 / -1;
  }
}
